import React, { useState } from 'react';

//    ' ' (onCreate) ' ' (onClose) .
function CreateRoomModal({ onCreate, onClose }) {
    const [roomName, setRoomName] = useState('');
    const [roomType, setRoomType] = useState('PUBLIC');
    const [password, setPassword] = useState('');

    const handleSubmit = (e) => {
        e.preventDefault();
        if (!roomName.trim()) {
            alert('  .');
            return;
        }
        //   onCreate    .
        onCreate({ roomName, roomType, password });
    };

    return (
        <div className="modal-overlay" onClick={onClose}>
            <div className="modal-content" onClick={(e) => e.stopPropagation()}>
                <button className="modal-close-btn" onClick={onClose}>&times;</button>
                <h2>  </h2>
                <form onSubmit={handleSubmit}>
                    <div className="form-group">
                        <label htmlFor="room-name"> </label>
                        <input
                            type="text"
                            id="room-name"
                            value={roomName}
                            onChange={(e) => setRoomName(e.target.value)}
                        />
                    </div>
                    <div className="form-group">
                        <label> </label>
                        <div className="radio-group">
                            <label>
                                <input type="radio" value="PUBLIC" checked={roomType === 'PUBLIC'} onChange={() => setRoomType('PUBLIC')} />
                                
                            </label>
                            <label>
                                <input type="radio" value="PRIVATE" checked={roomType === 'PRIVATE'} onChange={() => setRoomType('PRIVATE')} />
                                
                            </label>
                        </div>
                    </div>
                    {/*      . */}
                    {roomType === 'PRIVATE' && (
                        <div className="form-group">
                            <label htmlFor="room-password"></label>
                            <input
                                type="password"
                                id="room-password"
                                value={password}
                                onChange={(e) => setPassword(e.target.value)}
                            />
                        </div>
                    )}
                    <button type="submit" className="modal-submit-btn"></button>
                </form>
            </div>
        </div>
    );
}

export default CreateRoomModal;