import React, { useState, useContext } from 'react';
import { AuthContext } from '../context/AuthContext';

//   
const modalOverlayStyle = {
  position: 'fixed',
  top: 0,
  left: 0,
  width: '100%',
  height: '100%',
  backgroundColor: 'rgba(0, 0, 0, 0.5)',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
};

const modalContentStyle = {
  backgroundColor: 'white',
  padding: '20px 40px',
  borderRadius: '8px',
  boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
};

function AuthModal() {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
    const { login, closeLoginModal } = useContext(AuthContext); // Context login  .

  const handleSubmit = (e) => {
    e.preventDefault(); //       
    if (!username || !password) {
        alert('    .');
        return;
    }
    login(username, password);
    //      App.js  .
  };

  return (
    <div className="modal-overlay" style={modalOverlayStyle}>
      <div className="modal-content" onClick={(e) => e.stopPropagation()}>
        <button className="modal-close-btn" onClick={closeLoginModal}>&times;</button>
        <h2></h2>
        <form onSubmit={handleSubmit}>
          <div style={{ marginTop: '15px' }}>
            <label htmlFor="username" style={{ display: 'block', marginBottom: '5px' }}> </label>
            <input
              type="text"
              id="username"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              style={{ padding: '8px', width: '200px' }}
            />
          </div>
          <div style={{ marginTop: '15px' }}>
            <label htmlFor="password" style={{ display: 'block', marginBottom: '5px' }}></label>
            <input
              type="password"
              id="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              style={{ padding: '8px', width: '200px' }}
            />
          </div>
          <button type="submit" style={{ marginTop: '20px', padding: '10px 15px', width: '100%' }}>
            
          </button>
        </form>
      </div>
    </div>
  );
}

export default AuthModal;
