import React, { useState, useEffect, useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import { AuthContext } from '../context/AuthContext';
import { ChatContext } from '../context/ChatContext';
import CreateRoomModal from './CreateRoomModal';
import axiosInstance from '../api/axiosInstance';

const SERVER_URL = 'http://10.50.131.25:8080';

//    
const listItemStyle = {
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center',
  padding: '15px',
  borderBottom: '1px solid #eee',
  listStyle: 'none'
};

const roomInfoStyle = {
  flex: 1,
};

const roomActionsStyle = {
  marginLeft: '20px',
};

function LobbyPage() {
  const [rooms, setRooms] = useState([]);
  const { user, openLoginModal, loading  } = useContext(AuthContext);
    const { setActiveRoomId, initializeChat, usersByRoom } = useContext(ChatContext);
    const navigate = useNavigate();

  const [isCreateModalOpen, setIsCreateModalOpen] = useState(false);

  const fetchRooms = async () => {
    try {
        const response = await axiosInstance.get(`/room/list`);
        setRooms(response.data);
    } catch (error) {
        console.error('Failed to fetch rooms:', error);
    }
  };

  //     - async/await 
  const handleEnterRoom = async (room) => {
    let password = '';
    //     .
    if (room.roomType === 'PRIVATE' && !room.isMember) {
      password = prompt(' :');
      if (password === null) return;//     
        try {
            const response = await fetch(`${SERVER_URL}/room/${room.id}/users`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                credentials: 'include',
                body: JSON.stringify({ password }), //  body  
            });

            if (response.ok) {
                console.log(`Successfully entered room ${room.id}`);
                setActiveRoomId(room.id);
                setTimeout( function(){navigate(`/chat/${room.id}`)}, 100); //    
            } else {
                const errorData = await response.json();
                alert(errorData.message || ' .');
            }
        } catch (error) {
            console.error('Failed to enter room:', error);
            alert('  .');
        }
    }

    // ,      
      try {
          const response = await axiosInstance.post(`/room/${room.id}/users`, { password: '' }); //   
          navigate(`/chat/${room.id}`);
      } catch (error) {
          console.error('Failed to enter room:', error);
          alert(error.response?.data?.message || ' .');
      }
  };

  const handleCreateRoom = async (roomData) => {
        try {
            // roomData   { roomName, roomType, password } .
            const response = await axiosInstance.post(`/room/create`, roomData);
            const newRoomId = response.data;
            setIsCreateModalOpen(false); //  
            alert('  !');

            await initializeChat();

            fetchRooms(); //   
            navigate(`/chat/${newRoomId}`); //       
        } catch (error) {
            console.error('Failed to create room:', error);
            alert(error.response?.data?.message || '  .');
        }
  };

    useEffect(() => {
        //       .
        fetchRooms();
    }, []);

    useEffect(() => {
        //   ,    Context .
        setActiveRoomId(null);

        if (!loading) {
            fetchRooms();
        }
    }, [setActiveRoomId, loading]);

    //  4. usersByRoom      
    //    useEffect  .
    useEffect(() => {
        // usersByRoom       
        //   ,    
        //     .
        if (Object.keys(usersByRoom).length > 0) {
            fetchRooms();
        }
    }, [usersByRoom]); // usersByRoom    

  // if (!user) {
  //   return <h2 style={{padding: '20px'}}>   .</h2>;
  // }

    //  2.    '   ' '  ' .
    const myRooms = rooms.filter(room => room.isMember);
    const otherRooms = rooms.filter(room => !room.isMember);

    return (
        <div className="lobby-container">
            {/*  4. isCreateModalOpen true   . */}
            {isCreateModalOpen && <CreateRoomModal onCreate={handleCreateRoom} onClose={() => setIsCreateModalOpen(false)} />}

            <div className="lobby-header">
                <h1> </h1>
                <button
                    className="create-room-btn"
                    onClick={() => {
                        if (user) {
                            setIsCreateModalOpen(true);
                        } else {
                            alert(' .');
                            openLoginModal();
                        }
                    }}
                >
                      
                </button>
            </div>
            <div className="room-list">
                <h2> </h2>
                <ul>
                    {myRooms.length > 0 ? myRooms.map(room => (
                        <li key={room.id} className="room-list-item my-room" onClick={() => handleEnterRoom(room)}>
                            <div className="room-info">
                                <span className="room-name">
                                    {room.roomType === 'PRIVATE' && <span className="lock-icon"></span>}
                                    {room.name}
                                </span>
                                <span className="room-owner">: {room.ownerName}</span>
                            </div>
                            <div className="room-meta">
                                <span className="room-count">{room.connCount} / {room.userCount}</span>
                                <button className="enter-btn"></button>
                            </div>
                        </li>
                    )) : <p className="no-rooms">   .</p>}
                </ul>
            </div>
            {/* ---  4. ' '  UI ( UI ) --- */}
            <div className="room-list">
                <h2> </h2>
                <ul>
                    {otherRooms.length > 0 ? otherRooms.map(room => (
                        <li key={room.id} className="room-list-item" onClick={() => handleEnterRoom(room)}>
                            <div className="room-info">
                                <span className="room-name">
                                    {room.roomType === 'PRIVATE' && <span className="lock-icon"></span>}
                                    {room.name}
                                </span>
                                <span className="room-owner">: {room.ownerName}</span>
                            </div>
                            <div className="room-meta">
                                <span className="room-count">{room.connCount} / {room.userCount}</span>
                                <button className="enter-btn"></button>
                            </div>
                        </li>
                    )) : <p className="no-rooms">     .</p>}
                </ul>
            </div>
        </div>
    );
}

export default LobbyPage;
