import React, { useContext } from 'react';
import './App.css';
import Topbar from './components/Topbar';
import AuthModal from './components/AuthModal';
import { AuthContext } from './context/AuthContext';
import { Routes, Route } from 'react-router-dom'; // useParams  ChatPage   
import LobbyPage from './components/LobbyPage';
import ChatPage from './components/ChatPage'; // 1.  ChatPage  import .
import RegisterModal from './components/RegisterModal';
import MyProfileModal from "./components/MyProfileModal"; // RegisterModal import
import ProtectedRoute from './components/ProtectedRoute';

// ---   () ---
//          .
// --------------------------------

function App() {
    const { isLoginModalOpen, isRegisterModalOpen, isMyProfileModalOpen } = useContext(AuthContext);

  return (
    <div className="app-container">
        {isLoginModalOpen && <AuthModal />}
        {isRegisterModalOpen && <RegisterModal />} {/*     */}.
        {isMyProfileModalOpen && <MyProfileModal />}
      
      <Topbar />
      <main className="main-content">
        <Routes>
          {/* 2.  Route element    . */}
          <Route path="/" element={<LobbyPage />} />
            <Route
                path="/chat/:roomId"
                element={
                    <ProtectedRoute>
                        <ChatPage />
                    </ProtectedRoute>
                }
            />
        </Routes>
      </main>
    </div>
  );
}

export default App;
