import React, { useState, useContext } from 'react';
import { AuthContext } from '../context/AuthContext'; // AuthContext .

// AuthModal.js   .
const modalOverlayStyle = {
    position: 'fixed',
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    backgroundColor: 'rgba(0, 0, 0, 0.5)',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    zIndex: 1000,
};

const modalContentStyle = {
    backgroundColor: 'white',
    padding: '20px 40px',
    borderRadius: '8px',
    boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
};


function RegisterModal() {
    const [username, setUsername] = useState('');
    const [nickname, setNickname] = useState('');
    const [password, setPassword] = useState('');

    //  Context register  closeModal  .
    const { register, closeRegisterModal } = useContext(AuthContext);

    const handleSubmit = (e) => {
        e.preventDefault();
        if (!username || !nickname || !password) {
            alert('  .');
            return;
        }
        register(username, nickname, password);
    };

    return (
        <div className="modal-overlay" onClick={closeRegisterModal}>
            <div className="modal-content" onClick={(e) => e.stopPropagation()}>
                <button className="modal-close-btn" onClick={closeRegisterModal}>&times;</button>
                <h2></h2>
                <form onSubmit={handleSubmit}>
                    <div className="form-group">
                        <label htmlFor="reg-username"></label>
                        <input
                            type="text"
                            id="reg-username"
                            value={username}
                            onChange={(e) => setUsername(e.target.value)}
                        />
                    </div>
                    <div className="form-group">
                        <label htmlFor="reg-nickname"></label>
                        <input
                            type="text"
                            id="reg-nickname"
                            value={nickname}
                            onChange={(e) => setNickname(e.target.value)}
                        />
                    </div>
                    <div className="form-group">
                        <label htmlFor="reg-password"></label>
                        <input
                            type="password"
                            id="reg-password"
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                        />
                    </div>
                    <button type="submit" className="modal-submit-btn"></button>
                </form>
            </div>
        </div>
    );
}

export default RegisterModal;